.spin-center {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.spin-center .ant-spin-spinning {
  position: absolute;
  z-index: 9999;
}
.Dashboard {
  height: 100%;
}
.Dashboard .blank {
  height: 100%;
  padding: 0px 60px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.Dashboard .blank .Talk2Data {
  color: #000000;
  text-align: left;
  margin-right: 60px;
  margin-bottom: 57px;
}
.Dashboard .blank .Talk2Data h1 {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 51px;
  margin-top: 60px;
}
.Dashboard .blank .Talk2Data .step-box p {
  display: inline;
  font-size: 14px;
  color: #2A9BFF;
  text-align: left;
  line-height: 16px;
}
.Dashboard .blank .Talk2Data .step-box .num_style {
  border-radius: 50%;
  height: 21px;
  width: 21px;
  margin-right: 19px;
  display: inline-block;
  background: #FFFFFF;
  color: #80BEFF;
  line-height: 21px;
  text-align: center;
  border: 2px solid #80BEFF;
  white-space: nowrap;
  font-size: 16px;
  box-sizing: content-box;
}
.Dashboard .blank .Talk2Data .step-box span {
  margin-top: 24px;
  font-size: 14px;
  line-height: 20px;
  color: #000000;
  text-align: left;
}
.Dashboard .undraw_card {
  width: 466px;
  height: 267px;
  background: url(/static/media/undraw.5a67c8eb.png) no-repeat center;
